import React, {useCallback, useState} from 'react';
// noinspection ES6CheckImport
import {BrowserRouter, Redirect, Route, Switch} from "react-router-dom";
import './App.css';

import NewPlacePage from "./places/pages/newPlacePage";
import UsersPage from "./user/pages/usersPage";
import MainNavigation from "./shared/navigation/mainNavigation";
import UserPlacesPage from "./places/pages/userPlacesPage";
import UpdatePlacePage from "./places/pages/updatePlacePage";
import AuthPage from "./user/pages/authPage";
import {AuthContext} from "./shared/authContext";

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const login = useCallback(() => {
    setIsLoggedIn(true)
  }, []);
  const logout = useCallback(() => {
    setIsLoggedIn(false)
  }, []);

  let routes;
  if (isLoggedIn) {
    routes = (
      <Switch>
        <Route path='/' component={UsersPage} exact/>
        <Route path='/:userId/places' component={UserPlacesPage} exact/>
        <Route path='/places/new' component={NewPlacePage} exact/>
        <Route path='/places/:placeId' component={UpdatePlacePage}/>
        <Redirect to="/"/>
      </Switch>
    );
  } else {
    routes = (
      <Switch>
        <Route path='/' component={UsersPage} exact/>
        <Route path='/:userId/places' component={UserPlacesPage} exact/>
        <Route path='/auth' component={AuthPage}/>
        <Redirect to='/'/>
      </Switch>
    );
  }

  return (
    <AuthContext.Provider value={{isLoggedIn: isLoggedIn, login: login, logout: logout}}>
      <BrowserRouter>
        <MainNavigation/>
        <main>
          {routes}
        </main>
      </BrowserRouter>
    </AuthContext.Provider>
  );
}

export default App;
