import React, {useState, useContext} from 'react';

import s from './placesList.module.css';
import Card from "../../shared/UIElements/card";
import Button from "../../shared/formElements/button";
import Modal from "../../shared/UIElements/modal";
import Map from "../../shared/UIElements/map";
import {AuthContext} from "../../shared/authContext";

const PlaceItem = (props) => {
  const auth = useContext(AuthContext);
  const [showMap, setShowMap] = useState(false);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const openMapHandler = () => {
    setShowMap(true)
  };
  const closeMapHandler = () => {
    setShowMap(false)
  };
  const showDeleteWarningHandler = () => {
    setShowConfirmModal(true)
  };
  const cancelDeleteHandler = () => {
    setShowConfirmModal(false)
  };
  const confirmDeleteHandler = () => {
    setShowConfirmModal(false);
    console.log('Deleting...')
  };
  return (
    <React.Fragment>
      <Modal show={showMap}
             onCancel={closeMapHandler}
             header={props.address}
             contentClass={s.modalContent}
             footerClass={s.modalActions}
             footer={<Button onClick={closeMapHandler}>Close</Button>}>
        <div className={s.mapContainer}>
          <Map center={props.coordinates} zoom={16}/>
        </div>
      </Modal>
      <Modal show={showConfirmModal} onCancel={cancelDeleteHandler} header="Are you sure?" footerClass={s.modalActions} footer={
        <React.Fragment>
          <Button inverse onClick={cancelDeleteHandler}>Cancel</Button>
          <Button danger onClick={confirmDeleteHandler}>Delete</Button>
        </React.Fragment>}>
        <p>Do you want to proceed and delete this place? Please note that it can't be restored thereafter</p>
      </Modal>
      <li className={s.item}>
        <Card className={s.content}>
          <div className={s.image}>
            <img src={props.image} alt={props.title}/>
          </div>
          <div className={s.info}>
            <h2>{props.title}</h2>
            <h3>{props.address}</h3>
            <p>{props.description}</p>
          </div>
          <div className={s.actions}>
            <Button inverse onClick={openMapHandler}>View on Map</Button>
            {auth.isLoggedIn && <Button to={`/places/${props.id}`}>Edit</Button>}
            {auth.isLoggedIn && <Button danger onClick={showDeleteWarningHandler}>Delete</Button>}
          </div>
        </Card>
      </li>
    </React.Fragment>
  )
};
export default PlaceItem;
