import React, {useContext} from 'react';
import s from './navLinks.module.css';

import {NavLink} from "react-router-dom";
import {AuthContext} from "../authContext";

const NavLinks = (props) => {
  const auth = useContext(AuthContext);
  return (
    <ul className={s.navLinks}>
      <li><NavLink to="/" exact>All Users</NavLink></li>
      {auth.isLoggedIn && <li><NavLink to="/u1/places">My Places</NavLink></li>}
      {auth.isLoggedIn
        ? <li><NavLink to="/places/new">Add Place</NavLink></li>
        : <li><NavLink to="/auth">Add Place</NavLink></li>}
      {auth.isLoggedIn
        ? <li><button onClick={auth.logout}>Log out</button></li>
        : <li><NavLink to="/auth">Authenticate</NavLink></li>}
    </ul>
  )
};

export default NavLinks;
