import React ,{useState, useContext} from 'react';

import s from './auth.module.css';
import Card from "../../shared/UIElements/card";
import {VALIDATOR_EMAIL, VALIDATOR_MINLENGTH, VALIDATOR_REQUIRE} from "../../shared/utilities/validators";
import Input from "../../shared/formElements/input";
import {useForm} from "../../shared/hooks/formHook";
import Button from "../../shared/formElements/button";
import {AuthContext} from "../../shared/authContext";


const AuthPage = (props) => {
  const auth = useContext(AuthContext);
  const [isLoginMode, setIsLoginMode] = useState(true);
  const [formState, inputHandler, setFormData] = useForm({
    email: {
      value: '',
      isValid: false,
    },
    password: {
      value: '',
      isValid: false
    }
  }, false);
  const authSubmitHandler = (event) => {
    event.preventDefault();
    auth.login();
    console.log(formState.inputs)
  };
  const switchModeHandler = () => {
    if(!isLoginMode){
      setFormData({
        ...formState.inputs,
        name: undefined
      }, formState.inputs.email.isValid && formState.inputs.password.isValid)
    } else {
      setFormData({
        ...formState.inputs,
        name:{
          value: '',
          isValid: false
        }
      }, false)
    }
    setIsLoginMode(prevMode => !prevMode)
  };
  return <div className="center">
    <Card className={s.authentication}>
      <h2>{isLoginMode ? 'Login Required' : 'You have to Sign Up'}</h2>
      <hr/>
      <form onSubmit={authSubmitHandler}>
        <Input element='input'
               id='email' type='email' label='E-Mail'
               validators={[VALIDATOR_EMAIL()]}
               errorText='Please Enter a valid email address'
               onInput={inputHandler}/>
        <Input element='input'
               id='password' type='password' label='Password'
               validators={[VALIDATOR_MINLENGTH(5)]}
               errorText='Please Enter a valid password'
               onInput={inputHandler}/>
        {!isLoginMode && ( <Input element='input'
                                  id='name' type='text' label='Nickname'
                                  validators={[VALIDATOR_REQUIRE()]}
                                  errorText='Please Enter a name'
                                  onInput={inputHandler}/>)}
        <Button type="submit" disabled={!formState.isValid}>{isLoginMode ? 'Log in' : 'Sign up'}</Button>
      </form>
      <Button inverse onClick={switchModeHandler}>Switch to {!isLoginMode ? 'Log in' : 'Sign up'}</Button>
    </Card>
  </div>
};
export default AuthPage;
