import React from 'react';

import s from './modal.module.css';
import ReactDOM from "react-dom";
import {CSSTransition} from 'react-transition-group';
import Backdrop from "./backdrop";

const ModalOverlay = (props) => {
  const content = (
    <div className={`${s.modal} ${props.className}`} style={props.style}>
      <header className={s.header}>
        <h2>{props.header}</h2>
      </header>
      <form onSubmit={props.onSubmit ? props.onSubmit : event => event.preventDefault()}>
        <div className={`${s.content} ${props.contentClass}`}>
          {props.children}
        </div>
        <footer className={`${s.footer} ${props.footerClass}`}>
          {props.footer}
        </footer>
      </form>
    </div>
  );
  return ReactDOM.createPortal(content, document.getElementById('modalHook'));
};

const Modal = (props) => {
  return <React.Fragment>
    {props.show && <Backdrop onClick={props.onCancel}/>}
    <CSSTransition in={props.show} mountOnEnter unmountOnExit timeout={200} classNames="modal">
      <ModalOverlay {...props}/>
    </CSSTransition>
  </React.Fragment>
};

export default Modal;
