import React, {useEffect, useState} from 'react';
import char from "../../shared/images/Char.jpg";
import korhal from "../../shared/images/korhal.jpg";
import {useParams} from "react-router-dom";
import Input from "../../shared/formElements/input";
import {VALIDATOR_MINLENGTH, VALIDATOR_REQUIRE} from "../../shared/utilities/validators";
import Button from "../../shared/formElements/button";
import s from './places.module.css';
import {useForm} from "../../shared/hooks/formHook";
import Card from "../../shared/UIElements/card";

const DUMMY_PLACES = [
  {
    id: 'p1',
    title: 'char',
    description: 'a hell planet of smoke and fire',
    image: char,
    address: '1 Blizzard Way, Irvine, CA 92618, USA',
    location: {
      lat: 33.6580718,
      lng: -117.7693418
    },
    creator: 'u2'
  },
  {
    id: 'p2',
    title: 'korhal',
    description: 'a terran capital',
    image: korhal,
    address: '3100 Ocean Park Blvd, Santa Monica, CA 90405, USA',
    location: {
      lat: 34.019807,
      lng: -118.4546344
    },
    creator: 'u3'
  }
];

const UpdatePlacePage = () => {
  const [isLoading, setIsLoading] = useState(true);
  const placeId = useParams().placeId;
  const [formState, inputHandler, setFormData] = useForm({
    title: {
      value: '',
      isValid: false
    },
    description: {
      value: '',
      isValid: false
    }
  }, false);

  const identifiedPlace = DUMMY_PLACES.find(p => p.id === placeId);

  useEffect(() => {
    if(identifiedPlace){
      setFormData({
        title: {
          value: identifiedPlace.title,
          isValid: true
        },
        description: {
          value: identifiedPlace.description,
          isValid: true
        }
      }, true);
    }
    setIsLoading(false);
  }, [setFormData, identifiedPlace]);


  const placeUpdateSubmitHandler = (event) => {
    event.preventDefault();
    console.log(formState.inputs)
  };
  if (!identifiedPlace) {
    return <div className="center">
      <Card className={s.info}>
        <h2>could not find place!</h2>
      </Card>
    </div>
  }

  if(isLoading){
    return <div className="center">
      <h2>Loading ...!</h2>
    </div>
  }

  return (
      <form className={s.placeForm} onSubmit={placeUpdateSubmitHandler}>
        <Input id='title'
               element='input'
               label='Title'
               validators={[VALIDATOR_REQUIRE()]}
               errorText='Please enter a valid title'
               onInput={inputHandler}
               initialValue={formState.inputs.title.value}
               initialValid={formState.inputs.title.isValid}/>
        <Input id='description'
               element='textarea'
               label='Description'
               validators={[VALIDATOR_MINLENGTH(5)]}
               errorText='Please enter a valid description(minimum 5 symbols)'
               onInput={inputHandler}
               initialValue={formState.inputs.description.value}
               initialValid={formState.inputs.description.isValid}/>
        <Button type="submit" disabled={!formState.isValid}>Update Place</Button>
      </form>)
};

export default UpdatePlacePage;
